}

.sr-only {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

@font-face {
  font-family: "gulfs";
  src: url("../fonts/gulfsdisplay-normal-webfont.woff2") format("woff2"), url("../fonts/gulfsdisplay-normal-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@keyframes tic {
  0% {
    rotate: -15deg;
  }
  100% {
    rotate: 15deg;
  }
}
@keyframes defilement-rtl {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes tic-scale {
  0% {
    rotate: -5deg;
    scale: 1;
  }
  50% {
    scale: 1.1;
  }
  100% {
    rotate: 5deg;
    scale: 1;
  }
}
@keyframes beat-box {
  0% {
    scale: 1;
  }
  20% {
    scale: 1.2;
  }
  40% {
    scale: 1.1;
  }
  70% {
    scale: 1.2;
  }
  100% {
    scale: 1;
  }
}
body {
  font-family: "gulfs", sans-serif;
  font-size: 16px;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h3,
.h3 h4,
.h4 {
  font-family: "halogen", sans-serif;
  font-weight: 500;
  text-align: center;
  color: #231c4d;
}

h2,
.h2 {
  font-family: "gulfs", sans-serif;
  color: #FFFFFF;
  text-align: center;
  background-color: #ff316f;
  font-weight: normal;
  font-size: 18.75pt;
  line-height: 18.75pt;
  width: 320px;
  margin: 20px auto;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  padding: 5px 0;
}
h2::after, h2::before,
.h2::after,
.h2::before {
  content: "";
  background-image: url("./../img//SVG/star.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transform-origin: center center;
  translate: -50% 50%;
  display: flex;
  aspect-ratio: 1/1;
  position: absolute;
  z-index: 2;
  width: 25px;
  position: relative;
  bottom: 50%;
  translate: 0% 0%;
  animation: tic-scale alternate infinite 1s ease-in-out;
}
h2::after,
.h2::after {
  animation: tic-scale alternate-reverse infinite 1s ease-in-out;
}

h3, .h3 {
  font-family: "gulfs", sans-serif;
  text-align: left;
  font-size: 22.5pt;
  line-height: 22.5pt;
  margin: 0;
}

.back-top {
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: 40px;
  height: 70px;
  background-color: #ff316f;
  border-radius: 30px;
  border: 2px solid #FFFFFF;
  z-index: 20;
  transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  display: flex;
  justify-content: center;
  align-items: center;
}
.back-top::before {
  position: absolute;
  content: "";
  background-image: url("../../dist/img/SVG/top.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  width: 30px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.back-top.hide {
  bottom: -70px;
}

article {
  grid-column-start: 1;
  margin-bottom: 5vw;
  z-index: 5;
  grid-template-rows: auto 1fr;
  display: grid;
}
article .nom {
  grid-column-start: 1;
  width: 100%;
}
article .attributes {
  max-width: 128px;
  display: flex;
  flex-wrap: wrap;
  min-height: 30px;
  gap: 0px;
  flex-direction: row;
  align-content: flex-start;
  grid-row: 2;
}
article .attributes .type {
  font-size: 7.5pt;
  line-height: 7.5pt;
  line-height: 7.5pt;
  height: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  border: 1px solid;
  padding: 2% 5%;
  border-radius: 30px;
  font-family: "halogen", sans-serif;
}
article .attributes .type.magic {
  background: linear-gradient(45deg, #ff316f 0%, #FAC840 50%, #3e67ed 100%);
}

.header {
  width: 100%;
  height: fit-content;
  position: relative;
}
.header::before {
  content: "";
  background-image: url("./../img//SVG/globe.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transform-origin: center center;
  translate: -50% 50%;
  display: flex;
  aspect-ratio: 1/1;
  position: absolute;
  z-index: 2;
  width: 20vw;
  left: 0;
  bottom: 1vw;
}
.header::after {
  content: "";
  background-image: url("./../img//SVG/sunny.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transform-origin: center center;
  translate: -50% 50%;
  display: flex;
  aspect-ratio: 1/1;
  position: absolute;
  z-index: 2;
  width: 15vw;
  bottom: 0;
  left: 12vw;
  transform: scaleX(-1);
  animation: tic 5s infinite alternate ease-in-out;
}
.header h1 {
  color: #ffeec1;
  position: relative;
  height: 27vw;
  margin: 0;
}
.header h1::after {
  content: "";
  background-image: url("./../img/SVG/logo.svg");
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}

#page {
  background-color: #ffeec1;
  padding: 10px 8px;
  position: relative;
  overflow: hidden;
}
#page .roll-container {
  rotate: -8deg;
  display: grid;
  align-items: center;
  justify-content: center;
  justify-items: center;
  scale: 1.2;
  position: absolute;
  right: 4vw;
  top: 28vw;
}
#page .roll-container .roll {
  grid-column: 1;
  grid-row: 1;
  display: flex;
  align-items: center;
  right: 0;
  overflow: hidden;
  font-family: "halogen", sans-serif;
  width: 110px;
  height: 30px;
  font-size: 20pt;
  color: white;
}
#page .roll-container .roll span {
  white-space: nowrap;
  animation: defilement-rtl 10s infinite linear;
  z-index: 2;
}
#page .roll-container .roll.back {
  background-image: url("./../img/SVG/etiquette.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  width: 160px;
  height: 30px;
  top: -2px;
  position: relative;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

a:hover, a:active, a:focus {
  text-decoration: none;
}